import React, { Component, Fragment } from 'react'

import $ from 'jquery'

import { Consumer } from './context/Context'
class Form extends Component {
    state = {
        select: [
            {
                value: "0~500",
                text: "500以下"
            },
            {
                value: "501~1000",
                text: "501-1000 人"
            },
            {
                value: "1001",
                text: "1001 人以上"
            }
        ],
        checkbooxEmail: true,
        checkbooxPhone: true,
    }
    componentDidMount() {
        $(".form").fadeIn();
    }
    componentWillUnmount() {
        $(".form").fadeOut();
    }
    ChangeEmail = () => {
        this.setState({
            checkbooxEmail: !this.state.checkbooxEmail
        })
    }
    ChangePhone = () => {
        this.setState({
            checkbooxPhone: !this.state.checkbooxPhone
        })
    }
    render() {
        const { select, checkbooxPhone, checkbooxEmail } = this.state;
        return (
                <Consumer>
                    {value => (
                        <Fragment>
                            <a className="form__close" onClick={() => {value.openForm()}}><span></span><span></span></a>
                            <div className="form__box">
                            <form>
                                <div className="form__info">
                                <h2 className="title">填寫表單</h2>
                                <p className="text">取得 H.I.T 顧問神助攻解答解鎖完整影片與 IBM 關鍵趨勢報告</p>
                                </div>
                                <div className="form__group half">
                                    <input className="input" type="text" placeholder="姓⽒" value={value.lastName} onChange={value.onLastName}/>
                                    <input className="input" type="text" placeholder="名字" value={value.firstName} onChange={value.onFirstName}/>
                                </div>
                                <div className="form__group">
                                    <input className="input" type="tel" placeholder="電話號碼" maxLength="12" value={value.phone} onChange={value.onPhone}/>
                                </div>
                                <div className="form__group">
                                    <input className="input" type="text" placeholder="公司" value={value.company} onChange={value.onCompany}/>
                                </div>
                                <div className="form__group">
                                    <input className="input" type="text" placeholder="部⾨" value={value.dept} onChange={value.onDept}/>
                                </div>
                                <div className="form__group">
                                    <input className="input" type="text" placeholder="職稱" value={value.job} onChange={value.onJob}/>
                                </div>
                                <div className="form__group">
                                    <input className="input" type="email" placeholder="電⼦信箱" value={value.email} onChange={value.onEmail}/>
                                </div>
                                <div className="form__group">
                                    <select name="group" className="select" id="group" value={value.scale} onChange={value.onScale}>
                                        <option value="0" disabled>公司規模</option>
                                        {select.map(item => 
                                            <option key={item.text} value={item.value}>{item.text}</option>
                                        )}
                                    </select>
                                </div>
                                <div className="form__group check">
                                    <h3>IBM 得以使⽤我的聯絡⼈資料，以讓我得知產品、服務及供應⽅案的最新資訊</h3>
                                    <div className="box">
                                        <div>
                                            <input type="checkbox" id="email" name="email" value="email" onChange={this.ChangeEmail} checked={checkbooxEmail}/>
                                            <label htmlFor="email">透過電⼦郵件</label>
                                        </div>
                                        <div>
                                            <input type="checkbox" id="phone" name="phone" value="phone" onChange={this.ChangePhone} checked={checkbooxPhone}/>
                                            <label htmlFor="phone">透過電話</label>
                                        </div>
                                    </div>
                                </div>
                                <div className="form__group">
                                    <p>您可以隨時透過傳送電⼦郵件⾄ netsupp@us.ibm.com 來取消接收最新訊息。您也可以透過按⼀下每封此類電⼦郵件中的取消訂閱鏈結來取消訂閱接收⾏銷電⼦郵件。有關我們的處理作業詳細資訊可以在 IBM
                                            隱私權聲明。提交此表單時，即表⽰我確認我已閱讀並瞭解 IBM 隱私權聲明。</p>
                                </div>
                                <div className="form__group">
                                    <a className="submit" onClick={value.valid}>填寫完畢</a>
                                </div>
                            </form>
                            </div>
                        </Fragment>
                    )}
                </Consumer>
        )
    }
}

export default Form;