import React, { Component, Fragment } from 'react'
import YouTube from 'react-youtube';
import { createPortal } from 'react-dom'

import { Consumer } from './context/Context'

import $ from 'jquery'

export default class Video extends Component {
    state = {
        finish: true,
    }
    componentDidMount() {
        $(".video__popup").hide();
    }

    _onReady = (event) => {
        console.log("Ready!")
    }
    _onPlay = (event) => {
        if(!this.state.finish) return;
        this.setState({
            finish: false
        })
        setTimeout(() => {
            event.target.pauseVideo();
            $(".video__popup").fadeIn();
            // this.props.openForm();
        }, 30000)
    }

    open = () => {
        this.props.openForm(0);
    }

    render() {
        const opts = {
            playerVars: {
              autoplay: 0,
              controls: 1,
              playsinline: 1
            },
        };
        return createPortal(
            
            <Consumer>
                {value => (
                    <Fragment>
                        
                        <div className="video__show">
                            <div className="video__popup">
                                <p>留下資訊看顧問如何解答</p>
                                <a onClick={this.open}>留資訊看完整影片</a>
                            </div>
                            <YouTube videoId={value.videoItem[value.videoIdx].id} opts={opts} onPlay={this._onPlay} onReady={this._onReady} />
                        </div>
                        <div className="video__nav">
                            <ul>
                                {value.videoItem.map((item,idx) => 
                                    <li key={idx} className={item.state} onClick={() => {value.changeVideo(idx)}}
                                        style={{                            
                                            backgroundImage: `url(https://img.youtube.com/vi/${item.id}/default.jpg)`
                                        }}
                                    ></li>
                                )}
                            </ul>
                        </div>
                    </Fragment>
                )}
            </Consumer>
            ,document.getElementById("youtube")
        )
    }
}
